import styled, { StyledComponent } from '@emotion/styled'
import { css, SerializedStyles } from '@emotion/react'
import { pxToRem } from './utils'
import { DetailedHTMLProps, HTMLAttributes } from 'react'

/**
 * @Method: Returns an element with extra shorthand style attributes.
 * @Param {string}
 * @Return {string}
 */


export interface IDynamicProps {
  co?: string | null
  foWe?: string | null
  bold?: boolean | null | string
  foSi?: string | null
  w100?: boolean | null
  pa?: string | null
  wmax?: boolean | null
  mh?: string | null
  he?: string | null
  ma?: string | null
  baCo?: string | null
  label?: string | null
  ovAu?: boolean | null
  pointer?: boolean | null
  ellipsis?: boolean | null
  po?: string | null
  ov?: string | null
  hide?: boolean | null
  nowrap?: boolean | null
  z?: string | null
  z1?: boolean | null
  wi?: string | null
  maxw?: string | null
  op?: string | null
  top?: string | null
  bo?: string | null
  boRa?: string | null
  le?: string | null
  ri?: string | null
  liHe?: string | null
  maxh?: string | null
  foIt?: boolean | null
  minh?: string | null
  minw?: string | null
  uppercase?: boolean | null
  lowercase?: boolean | null  
  vw100?: boolean | null
  vh100?: boolean | null
  h100?: boolean | null
  notvisible?: boolean | null
  txal?: 'center' | 'left' | 'right' | 'justify' | string | null
  shadow?: {baCo: string, size: string} | null
  hoLine?: boolean | null  
  noSel?: boolean | null
  scale?: number | null
  zoom?: number | null
  foFam?: string | null
  hoOp?: string | number | null
  asRa?: number | string | boolean | null
  bor?: string | null
  borWi?: string | null
  woBr?: string | null
  trTi?: string | null
  trFo?: string | null
  ouLi?: string | null
  capa?: boolean | null

}

/**
 * @Method: IFlexProps
 */
export interface IFlexProps {
  f?: boolean | null;
  fc?: boolean | null;
  flex?: string | null;
  frtl?: boolean | null;
  frtr?: boolean | null;
  frbr?: boolean | null;
  frcl?: boolean | null;
  frbl?: boolean | null;
  frcr?: boolean | null;
  frtc?: boolean | null;
  frbc?: boolean | null;
  frcc?: boolean | null;
  fctr?: boolean | null;
  fctl?: boolean | null;
  fcbr?: boolean | null;
  fccr?: boolean | null;
  fccl?: boolean | null;
  fctc?: boolean | null;
  fcbc?: boolean | null;
  fcbl?: boolean | null;
  fccc?: boolean | null;
  fw?: boolean | null;
  fwr?: boolean | null
  f1?: boolean | null;
  f2?: boolean | null;
  f3?: boolean | null;
  fs0?: boolean | null;
  fsb?: boolean | null;
  fse?: boolean | null;
  fsa?: boolean | null;
  fflex?: string | null;
  gap?: string | null;
  
}
export interface HyperLinkTag extends HTMLAnchorElement, StyledComponent<DetailedHTMLProps<HTMLAttributes<HTMLAnchorElement>, HTMLAnchorElement>, IQjuulProps> {}
export interface IQjuulProps extends IFlexProps, IDynamicProps {}

class Elements {
  flex: (props: IFlexProps) => SerializedStyles
  dynamic: (props: IDynamicProps) => SerializedStyles
  h: SerializedStyles
  h1: StyledComponent<DetailedHTMLProps<HTMLAttributes<HTMLHeadingElement>, HTMLHeadingElement>, IQjuulProps>
  h2: StyledComponent<DetailedHTMLProps<HTMLAttributes<HTMLHeadingElement>, HTMLHeadingElement>, IQjuulProps>
  h3: StyledComponent<DetailedHTMLProps<HTMLAttributes<HTMLHeadingElement>, HTMLHeadingElement>, IQjuulProps>
  h4: StyledComponent<DetailedHTMLProps<HTMLAttributes<HTMLHeadingElement>, HTMLHeadingElement>, IQjuulProps>
  h5: StyledComponent<DetailedHTMLProps<HTMLAttributes<HTMLHeadingElement>, HTMLHeadingElement>, IQjuulProps>
  h6: StyledComponent<DetailedHTMLProps<HTMLAttributes<HTMLHeadingElement>, HTMLHeadingElement>, IQjuulProps>
  spacer: StyledComponent<DetailedHTMLProps<HTMLAttributes<HTMLDivElement>, HTMLDivElement>, IQjuulProps>
  main: StyledComponent<DetailedHTMLProps<HTMLAttributes<HTMLElement>, HTMLElement>, IQjuulProps>
  p: StyledComponent<DetailedHTMLProps<HTMLAttributes<HTMLParagraphElement>, HTMLParagraphElement>, IQjuulProps>
  div: StyledComponent<DetailedHTMLProps<HTMLAttributes<HTMLDivElement>, HTMLDivElement>, IQjuulProps>
  header: StyledComponent<DetailedHTMLProps<HTMLAttributes<HTMLElement>, HTMLElement>, IQjuulProps>
  section: StyledComponent<DetailedHTMLProps<HTMLAttributes<HTMLElement>, HTMLElement>, IQjuulProps>
  cssp: SerializedStyles
  p1: StyledComponent<DetailedHTMLProps<HTMLAttributes<HTMLParagraphElement>, HTMLParagraphElement>, IQjuulProps>
  p2: StyledComponent<DetailedHTMLProps<HTMLAttributes<HTMLParagraphElement>, HTMLParagraphElement>, IQjuulProps>
  p3: StyledComponent<DetailedHTMLProps<HTMLAttributes<HTMLParagraphElement>, HTMLParagraphElement>, IQjuulProps>
  p4: StyledComponent<DetailedHTMLProps<HTMLAttributes<HTMLParagraphElement>, HTMLParagraphElement>, IQjuulProps>
  p5: StyledComponent<DetailedHTMLProps<HTMLAttributes<HTMLParagraphElement>, HTMLParagraphElement>, IQjuulProps>
  p6: StyledComponent<DetailedHTMLProps<HTMLAttributes<HTMLParagraphElement>, HTMLParagraphElement>, IQjuulProps>
  a: HyperLinkTag
  ins: StyledComponent<DetailedHTMLProps<HTMLAttributes<HTMLModElement>, HTMLModElement>, IQjuulProps>
  li: StyledComponent<DetailedHTMLProps<HTMLAttributes<HTMLLIElement>, HTMLLIElement>, IQjuulProps>
  ul: StyledComponent<DetailedHTMLProps<HTMLAttributes<HTMLUListElement>, HTMLUListElement>, IQjuulProps>
  constructor() {
    this.flex = (props: IFlexProps) => css`
      ${props.frtl && 'display:flex; justify-content: flex-start; align-items: flex-start;'};
      ${props.frtr && 'display:flex; justify-content: flex-end; align-items: flex-start;'};
      ${props.frbl && 'display:flex; justify-content: flex-start; align-items: flex-end;'};
      ${props.frbr && 'display:flex; justify-content: flex-end; align-items: flex-end;'};
      ${props.frcl && 'display:flex; justify-content: flex-start; align-items: center;'};
      ${props.frcr && 'display:flex; justify-content: flex-end; align-items: center;'};
      ${props.frtc && 'display:flex; justify-content: center; align-items: flex-start;'};
      ${props.frbc && 'display:flex; justify-content: center; align-items: flex-end;'};
      ${props.frcc && 'display:flex; justify-content: center; align-items: center;'};
      ${props.fctl && 'display:flex; flex-direction: column; align-items: flex-start; justify-content: flex-start;'};
      ${props.fctr && 'display:flex; flex-direction: column; align-items: flex-end; justify-content: flex-start;'};
      ${props.fcbl && 'display:flex; flex-direction: column; align-items: flex-start; justify-content: flex-end;'};
      ${props.fcbr && 'display:flex; flex-direction: column; align-items: flex-end; justify-content: flex-end;'};
      ${props.fccl && 'display:flex; flex-direction: column; align-items: flex-start; justify-content: center;'};
      ${props.fccr && 'display:flex; flex-direction: column; align-items: flex-end; justify-content: center;'};
      ${props.fctc && 'display:flex; flex-direction: column; align-items: center; justify-content: flex-start;'};
      ${props.fcbc && 'display:flex; flex-direction: column; align-items: center; justify-content: flex-end;'};
      ${props.fccc && 'display:flex; flex-direction: column; align-items: center; justify-content: center;'};
      ${props.fw && 'flex-wrap: wrap;'};
      ${props.fwr && `flex-wrap: wrap-reverse;`}
      ${props.f1 && 'flex: 1;'};
      ${props.f2 && 'flex: 2;'};
      ${props.f3 && 'flex: 3;'};
      ${props.fflex && `flex: ${props.fflex}`};
      ${props.fs0 && 'flex-shrink: 0;'};
      ${props.fsb && 'justify-content: space-between;'};
      ${props.fsa && 'justify-content: space-around;'};
      ${props.fse && 'justify-content: space-evenly;'};
      ${props.gap && `gap: ${props.gap};`}
    `
    this.dynamic = (props: IDynamicProps): any => css`
      ${props.co && `color: ${props.co};`};
      ${props.foWe && `font-weight: ${props.foWe};`};
      ${props.bold && 'font-weight: 700;'};
      ${props.foSi && `font-size: ${pxToRem(props.foSi)};`};
      ${props.w100 && 'width: 100%;'};
      ${props.pa && `padding: ${props.pa};`};
      ${props.ma && `margin: ${props.ma};`};
      ${props.baCo && `background-color: ${props.baCo};`};
      ${props.label && `label: -${props.label};`};
      ${props.ovAu && `overflow: auto;`};
      ${props.pointer && `cursor: pointer`};
      ${props.hoLine && `&:hover { text-decoration: underline;}`};
      ${props.noSel && 
        `
        -webkit-touch-callout: none;
        -webkit-user-select: none;
        -khtml-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
       `
      }
      ${props.scale && `transform: scale(${props.scale});`}
      ${props.zoom && `zoom: ${props.zoom};`}
      ${props.foFam && `  font-family: ${props.foFam};`}

      ${props.ellipsis && `
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        `
      }
      ${props.hoOp && `&:hover { opacity: ${props.hoOp};}`};
      ${props.asRa && (
        typeof props.asRa === 'boolean' ? 'aspect-ratio: 1;' : `aspect-ratio: ${props.asRa};`
      )}
      ${props.bor && `border: ${props.bor};`}
      ${props.borWi && `border-width: ${props.borWi};`}
      ${props.woBr && `word-break: ${props.woBr};`}
      ${props.trTi && `transition: ${props.trTi};`}
      ${props.trFo && `transform: ${props.trFo};`}
      ${props.ouLi && `outline: ${props.ouLi};`}
      ${props.capa && ` text-transform: capitalize;`}

      ${props.uppercase && 'text-transform: uppercase;'}
      ${props.lowercase && 'text-transform: lowercase;'}
      ${props.po && `position: ${props.po};`}
      ${props.ov && `overflow: ${props.ov};`}
      ${props.hide && 'display: none;'}
      ${props.nowrap && 'white-space: nowrap;'}
      ${props.z1 && 'z-index: 1;'}
      ${props.z && `z-index: ${props.z};`}
      ${props.wi && `width: ${props.wi};`}
      ${props.maxw && `max-width: ${props.maxw};`}
      ${props.op && `opacity: ${props.op};`}
      ${props.top && `top: ${props.top};`}
      ${props.bo && `bottom: ${props.bo};`}
      ${props.boRa && `border-radius: ${props.boRa};`}
      ${props.le && `left: ${props.le};`}
      ${props.ri && `right: ${props.ri};`}
      ${props.maxh && `max-height: ${props.maxh};`}
      ${props.minw && `min-width: ${props.minw};`}
      ${props.minh && `min-height: ${props.minh};`}
      ${props.liHe && `line-height: ${props.liHe};`}
      ${props.he && `height: ${props.he};`};
      ${props.foIt && `font-style: italic;`}
      ${props.vw100 && 'width: 100vw;'}
      ${props.vh100 && 'height: 100vh;'}

      ${props.h100 && 'height: 100%;'}
      ${props.notvisible && `visibility: hidden;`}
      ${props.txal && `text-align: ${props.txal};`}
      ${props.shadow && `box-shadow: calc(${props.shadow.size} / 2) ${props.shadow.size} calc(${props.shadow.size} * 0.8) ${props.shadow.size} ${props.shadow.baCo};`}
    `
    this.h = css`
      color: inherit;
      line-height: 1.2;
    `
    this.cssp = css`
      color: inherit;
      font-weight: 400;
      line-height: 1.2;
    `
    this.h1 = styled.h1`
      ${this.h};
      label: -h1;
      font-size: ${pxToRem('35px')};
      ${this.flex as any};
      ${this.dynamic as any};
    `
    this.h2 = styled.h2`
      ${this.h};
      label: -h2;
      font-size: ${pxToRem('22px')};
      ${this.flex as any};
      ${this.dynamic as any};
    `
    this.h3 = styled.h3`
      ${this.h};
      label: -h3;
      font-size: ${pxToRem('18px')};
      ${this.flex as any};
      ${this.dynamic as any};
    `
    this.h4 = styled.h4`
      ${this.h};
      label: -h4;
      font-size: ${pxToRem('16px')};
      ${this.flex as any};
      ${this.dynamic as any};
    `
    this.h5 = styled.h5`
      ${this.h};
      label: -h5;
      font-size: ${pxToRem('14px')};
      ${this.flex as any};
      ${this.dynamic as any};
    `
    this.h6 = styled.h6`
      ${this.h};
      label: -h6;
      font-size: ${pxToRem('12px')};
      ${this.flex as any};
      ${this.dynamic as any};
    `
    this.p1 = styled.p`
      ${this.cssp};
      label: -p2;
      font-size: ${pxToRem('22px')};
      ${this.flex as any};
      ${this.dynamic as any};
    `
    this.p2 = styled.p`
      ${this.cssp};
      label: -p2;
      font-size: ${pxToRem('22px')};
      ${this.flex as any};
      ${this.dynamic as any};
    `
    this.p3 = styled.p`
      ${this.cssp};
      label: -p3;
      font-size: ${pxToRem('18px')};
      ${this.flex as any};
      ${this.dynamic as any};
    `
    this.p4 = styled.p`
      ${this.cssp};
      label: -p4;
      font-size: ${pxToRem('16px')};
      ${this.flex as any};
      ${this.dynamic as any};
    `
    this.p5 = styled.p`
      ${this.cssp};
      label: -p5;
      font-size: ${pxToRem('14px')};
      ${this.flex as any};
      ${this.dynamic as any};
    `
    this.p6 = styled.p`
      ${this.cssp};
      label: -p6;
      font-size: ${pxToRem('11px')};
      ${this.flex as any};
      ${this.dynamic as any};
    `
    this.main = styled.main`
      label: -main;
      ${this.flex as any};
      ${this.dynamic as any};
    `
    this.spacer = styled.div`
      label: -spacer;
      flex: 1;
      ${this.flex as any};
      ${this.dynamic as any};
    `
    this.p = styled.p`
      label: -p;
      ${this.flex as any};
      ${this.dynamic as any};
    `
    this.div = styled.div`
      label: -div;
      ${this.flex as any};
      ${this.dynamic as any};
    `
    this.header = styled.header`
      label: -div;
      ${this.flex as any};
      ${this.dynamic as any};
    `
    this.section = styled.section`
      label: -section;
      ${this.flex as any};
      ${this.dynamic as any};
    `
    this.a = styled.a`
      label: -anchor;
      text-decoration: none;
      color: inherit;
      ${this.flex as any};
      ${this.dynamic as any};
    `
    this.ins = styled.ins`
      label: -insert;
      color: inherit;
      ${this.flex as any};
      ${this.dynamic as any};
    `
    this.ul = styled.ul`
      label: -ul;
      color: inherit;
      padding: 0;
      margin: 0;
      ${this.flex as any};
      ${this.dynamic as any};
    `
    this.li = styled.li`
      list-style-type: circle;
      padding: 0;
      label: -ul;
      color: inherit;
      ${this.flex as any};
      ${this.dynamic as any};
    `
  }
}

const q = new Elements()

export default q
